"use client";
import React, { useState, useEffect } from "react";
import Link from "next/link";
import Image from "next/image";
import { Menu, X } from "lucide-react";

const Navbar = () => {
  const [isOpen, setIsOpen] = useState(false);

  // Close menu on ESC, outside click, or back button
  useEffect(() => {
    const handleKeyDown = (e) => e.key === "Escape" && setIsOpen(false);
    const handleClickOutside = (e) => {
      if (e.target.id === "menu-overlay") setIsOpen(false);
    };
    const handlePopState = () => setIsOpen(false);

    window.addEventListener("keydown", handleKeyDown);
    document.addEventListener("click", handleClickOutside);
    window.addEventListener("popstate", handlePopState);

    return () => {
      window.removeEventListener("keydown", handleKeyDown);
      document.removeEventListener("click", handleClickOutside);
      window.removeEventListener("popstate", handlePopState);
    };
  }, []);

  const toggleMenu = () => {
    if (!isOpen) window.history.pushState({ menu: true }, "");
    else window.history.back();
    setIsOpen(!isOpen);
  };

  const closeMenu = () => {
    if (isOpen) window.history.back();
    setIsOpen(false);
  };

  const navItems = [
    "Home",
    "About",
    "Services",
    "Portfolio",
    "Experts",
    "Blogs",
    "Contact",
    "Verify",
  ];

  return (
    <nav className="fixed top-[40px] left-0 w-full bg-bg-light/90 backdrop-blur-md border-b border-gray-200 z-50">
      <div className="max-w-7xl mx-auto flex items-center justify-between px-6 lg:px-10 py-3">
        {/* Logo */}
        <Link href="/" className="flex items-center gap-2">
          <Image
            src="/assets/images/logo.png"
            alt="RevoticAi Logo"
            width={130}
            height={90}
            className="rounded-md"
          />
          <span className="font-semibold text-lg text-text-heading">
            RevoticAi
          </span>
        </Link>

        {/* Desktop Menu */}
        <div className="hidden md:flex items-center gap-8 font-medium text-text-heading">
          {navItems.map((item) => (
            <Link
              key={item}
              href={`/${item === "Home" ? "" : item.toLowerCase()}`}
              className="relative text-sm group transition-colors"
            >
              {item}
              <span className="absolute left-0 -bottom-1 w-0 h-[2px] bg-primary transition-all duration-300 group-hover:w-full"></span>
            </Link>
          ))}

          <Link
            href="/quote"
            className="ml-4 bg-primary text-black text-sm px-5 py-2 rounded-full hover:bg-primary-hover transition-all"
          >
            Book A Free Call
          </Link>
        </div>

        {/* Mobile Menu Button */}
        <button
          className="md:hidden text-text-heading focus:outline-none"
          onClick={toggleMenu}
        >
          {isOpen ? <X size={26} /> : <Menu size={26} />}
        </button>
      </div>

      {/* Mobile Drawer */}
      {isOpen && (
        <div
          id="menu-overlay"
          className="fixed inset-0 bg-black/40 z-40 flex md:hidden transition-all duration-300"
        >
          <div className="w-72 bg-bg-light/95 backdrop-blur-md h-full p-6 ml-auto relative animate-slide-in-right">
            {/* Close Button */}
            <button
              onClick={closeMenu}
              className="absolute top-5 right-5 text-text-body hover:text-text-heading transition"
            >
              <X size={26} />
            </button>

            {/* Mobile Logo */}
            <div className="flex items-center gap-2 mb-8 mt-2">
              <Image
                src="/assets/images/logo.png"
                alt="RevoticAi Logo"
                width={40}
                height={40}
                className="rounded-md"
              />
              <span className="font-semibold text-lg text-text-heading">
                RevoticAi
              </span>
            </div>

            {/* Mobile Nav Links */}
            <nav className="flex flex-col gap-5 font-medium text-text-heading">
              {navItems.map((item) => (
                <Link
                  key={item}
                  href={`/${item === "Home" ? "" : item.toLowerCase()}`}
                  onClick={closeMenu}
                  className="hover:text-primary transition-colors text-[15px]"
                >
                  {item}
                </Link>
              ))}

              <Link
                href="/quote"
                onClick={closeMenu}
                className="mt-6 bg-primary text-black px-4 py-2 rounded-full text-center hover:bg-primary-hover transition-all"
              >
                Book A Free Call
              </Link>
            </nav>
          </div>
        </div>
      )}
    </nav>
  );
};

export default Navbar;
